//查询所有考试
<template>
  <div class="exam">
    <el-table :data="pagination.records" border>
      <el-table-column
        fixed="left"
        prop="source"
        label="考试科目"
        width="120"
      ></el-table-column>
      <el-table-column
        prop="type"
        label="考试描述"
        width="180"
      ></el-table-column>
      <!-- <el-table-column
        prop="description"
        label="介绍"
        width="200"
      ></el-table-column> -->
      <!-- <el-table-column
        prop="institute"
        label="所属学院"
        width="180"
      ></el-table-column> -->
      <el-table-column
        prop="major"
        label="所属专业"
        width="200"
      ></el-table-column>
      <el-table-column prop="grade" label="年级" width="100"></el-table-column>
      <el-table-column
        prop="examDate"
        label="考试日期"
        width="120"
      ></el-table-column>
      <el-table-column
        prop="totalTime"
        label="持续时间"
        width="120"
      ></el-table-column>
      <el-table-column
        prop="totalScore"
        label="总分"
        width="120"
      ></el-table-column>
      <el-table-column
        prop="tips"
        label="考生须知"
        width="400"
      ></el-table-column>
      <el-table-column fixed="right" label="操作" width="220">
        <template slot-scope="scope">
          <el-button
            @click="edit(scope.row.examCode)"
            type="primary"
            size="small"
            >编辑</el-button
          >
          <el-button
            @click="create(scope.row.paperId)"
            type="success"
            size="small"
            >组卷</el-button
          >
          <el-button
            @click="deleteRecord(scope.row.examCode)"
            type="danger"
            size="small"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pagination.current"
      :page-sizes="[5, 8, 10]"
      :page-size="pagination.size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pagination.total"
      class="page"
    >
    </el-pagination>

    <el-dialog
      title="在线组卷"
      :visible.sync="flag"
      @close="resetForm('score')"
      width="300px"
    >
      <el-form :model="score" ref="score">
        <el-form-item
          label="选择题数量"
          :label-width="formLabelWidth"
          prop="changeNumber"
        >
          <el-input
            v-model="score.changeNumber"
            placeholder="请输入选择题数量"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="填空题数量"
          :label-width="formLabelWidth"
          prop="fillNumber"
        >
          <el-input
            v-model="score.fillNumber"
            placeholder="请输入填空题数量"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="判断题数量"
          :label-width="formLabelWidth"
          prop="judgeNumber"
        >
          <el-input
            v-model="score.judgeNumber"
            placeholder="请输入判断题数量"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="flag = false">取 消</el-button>
        <el-button type="primary" @click="submitExam()">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 编辑对话框-->
    <el-dialog
      title="编辑考试信息"
      :visible.sync="dialogVisible"
      width="30%"
      @close="resetForm('form')"
    >
      <section class="update">
        <el-form ref="form" :rules="rules2" :model="form" label-width="80px">
          <el-form-item label="考试科目" prop="source">
            <el-input v-model="form.source"></el-input>
          </el-form-item>
          <!-- <el-form-item label="介绍">
            <el-input v-model="form.description"></el-input>
          </el-form-item> -->
          <el-form-item label="考试描述" prop="type">
            <el-input v-model="form.type"></el-input>
          </el-form-item>
          <!-- <el-form-item label="所属学院">
            <el-input v-model="form.institute"></el-input>
          </el-form-item> -->
          <el-form-item label="所属专业" prop="major">
            <el-input v-model="form.major"></el-input>
          </el-form-item>
          <el-form-item label="年级">
            <el-input v-model="form.grade"></el-input>
          </el-form-item>
          <el-form-item label="考试日期" prop="examDate">
            <el-col :span="11">
              <el-date-picker
                type="date"
                placeholder="选择日期"
                v-model="form.examDate"
                style="width: 100%;"
              ></el-date-picker>
            </el-col>
          </el-form-item>
          <el-form-item label="持续时间" prop="totalTime">
            <el-input v-model="form.totalTime"></el-input>
          </el-form-item>
          <el-form-item label="总分" prop="totalScore">
            <el-input v-model="form.totalScore"></el-input>
          </el-form-item>
          <el-form-item label="考生须知">
            <el-input type="textarea" v-model="form.tips"></el-input>
          </el-form-item>
        </el-form>
      </section>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submit()">提 交</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formLabelWidth: "100px",
      flag: false,
      paperId: "",
      score: {
        changeNumber: "",
        fillNumber: "",
        judgeNumber: ""
      },
      form: {}, //保存点击以后当前试卷的信息
      pagination: {
        //分页后的考试信息
        current: 1, //当前页
        total: null, //记录条数
        size: 5 //每页条数
      },
      dialogVisible: false,
      rules2: {
        source: [
          { required: true, message: "考试科目不能为空", trigger: "blur" }
        ],
        major: [
          { required: true, message: "请选择所属专业", trigger: "change" }
        ],
        grade: [{ required: true, message: "请选择年级", trigger: "change" }],
        examDate: [
          { required: true, message: "请选择考试日期", trigger: "change" }
        ],
        totalTime: [
          { required: true, message: "考试时长不能为空", trigger: "blur" }
        ],
        type: [
          { required: true, message: "考试描述不能为空", trigger: "blur" }
        ],
        totalScore: [
          { required: true, message: "总分不能为空", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.getExamInfo();
  },
  methods: {
    submitExam(paperId) {
      this.$confirm("一旦组卷之后将不可再修改，是否确定?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$axios({
            url: "/api/item",
            method: "post",
            data: {
              changeNumber: this.score.changeNumber,
              fillNumber: this.score.fillNumber,
              judgeNumber: this.score.judgeNumber,
              paperId: this.paperId,
              subject: "大学生英语四级" //题目数量太少，指定为xxx试卷出题
            }
          }).then(res => {
            console.log(res);
            let data = res.data;
            if (data.code == 200) {
              // setTimeout(() => {
              //   this.$router.push({ path: "/selectAnswer" });
              // }, 1000);
              this.flag = false;
              this.$message({
                message: data.message,
                type: "success"
              });
            } else if (data.code == 400) {
              this.$message({
                message: data.message,
                type: "error"
              });
            }
          });
        })
        .catch(() => {
          this.flag = false;
          this.$message({
            type: "info",
            message: "取消组卷"
          });
        });
    },
    create(paperId) {
      this.flag = true;
      this.paperId = paperId;
      console.log(this.paperId);
    },
    edit(examCode) {
      //编辑试卷
      this.dialogVisible = true;
      this.$axios(`/api/exam/${examCode}`).then(res => {
        //根据试卷id请求后台
        if (res.data.code == 200) {
          this.form = res.data.data;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // handleClose(done) {
    //   //关闭提醒
    //   this.$confirm("确认关闭？")
    //     .then(_ => {
    //       done();
    //     })
    //     .catch(_ => {});
    // },
    submit() {
      let formName = "form";
      this.$refs[formName].validate(valid => {
        if (valid) {
          //提交修改后的试卷信息
          this.dialogVisible = false;
          this.$axios({
            url: "/api/exam",
            method: "put",
            data: {
              ...this.form
            }
          }).then(res => {
            if (res.data.code == 200) {
              this.$message({
                //成功修改提示
                message: "更新成功",
                type: "success"
              });
            }
            this.getExamInfo();
          });
        } else {
          return false;
        }
      });
    },
    deleteRecord(examCode) {
      this.$confirm("确定删除该记录吗,该操作不可逆！！！", "删除提示", {
        confirmButtonText: "确定删除",
        cancelButtonText: "算了,留着",
        type: "danger"
      })
        .then(() => {
          //确认删除
          this.$axios({
            url: `/api/exam/${examCode}`,
            method: "delete"
          }).then(res => {
            this.getExamInfo();
          });
        })
        .catch(() => {});
    },
    getExamInfo() {
      //分页查询所有试卷信息
      this.$axios(
        `/api/exams/${this.pagination.current}/${this.pagination.size}`
      )
        .then(res => {
          this.pagination = res.data.data;
        })
        .catch(error => {});
    },
    //改变当前记录条数
    handleSizeChange(val) {
      this.pagination.size = val;
      this.getExamInfo();
    },
    //改变当前页码，重新发送请求
    handleCurrentChange(val) {
      this.pagination.current = val;
      this.getExamInfo();
    }
  }
};
</script>
<style lang="scss" scoped>
.exam {
  padding: 0px 40px;
  .page {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .edit {
    margin-left: 20px;
  }
}
/deep/ .cell {
  text-align: center;
}
</style>



